<template>
  <el-dialog v-model="dialogVisible" size="full" :show-close="false" @close="onClose">
    <div slot="title">
      <div class="el-dialog__headerbtn">
        <router-link :to="{name: 'UserProfile'}"><i class="el-dialog__close el-icon el-icon-close"></i></router-link>
      </div>
    </div>
    <slot>
      <div class="container" style="padding-top:125px;">
        <h3>Update Avatar</h3>
        <div class="modal-body">
          <div class="row nav-selector">
            <ul class="nav nav-pills">
              <li class="nav-item">
                <a class="nav-link active" href="#upload" data-toggle="pill" @click="setTab(0)"><b>Upload</b></a>
              </li>
              <li class="nav-item">
                <a class="nav-link" href="#uploadURL" data-toggle="pill" @click="setTab(1)"><b>Image From URL</b></a>
              </li>
            </ul>
          </div>
          <div class="tab-content">
            <div class="tab-pane active" id="upload" role="tabpanel">
              <div class="row user-row">
                <el-input  placeholder="Upload Your Photo" v-model="file.url" disabled>
                  <template slot="append">
                    <el-upload
                      class="upload-demo"
                      action=""
                      :show-upload-list="false"
                      :multiple="false"
                      :before-upload="onFileSelected">
                      <el-button size="small" type="primary"  style="color:#212121;">Upload Files</el-button>
                    </el-upload>
                  </template>
                </el-input>
              </div>
            </div>
            <div class="tab-pane" id="uploadURL" role="tabpanel">
              <div class="row user-row">
                <el-input placeholder="https://www." v-model="imageUrl">
                </el-input>
              </div>
            </div>
          </div>
        </div>
        <div class="footer" style="margin-top:20px;">
          <el-button type="primary "@click="uploadLocal">Upload</el-button>
          <router-link :to="{name: 'UserProfile'}"><el-button @click="dialogVisible = false">Cancel</el-button></router-link>
        </div>
      </div>
    </slot>
  </el-dialog>
</template>
<style>
</style>
<script>
  import { db } from '../../../../firebase/vueFire'
  import fireBase from '../../../../firebase/vueFire'
  import { Message } from 'element-ui'
  import _ from 'lodash'
  export default {
    data () {
      return {
        tab: 0,
        dialogVisible: true,
        imageUrl: '',
        file: {
          url: '',
          obj: null
        }
      }
    },
    mounted () {
      this.$ga.trackPage(this.$route.path)
    },
    methods: {
      setTab (id) {
        this.tab = id
      },
      onClose () {
        this.file = {}
        this.$router.push({name: 'UserProfile'})
      },
      onFileSelected (file) {
        this.file.obj = file
        this.file.url = file.name
        return false
      },
      uploadLocal () {
        if (this.tab) {
          if (!_.isEmpty(this.imageUrl)) {
            var user = fireBase.auth().currentUser
            user.updateProfile({
              photoURL: this.imageUrl
            }).then(() => {
              Message({
                showClose: true,
                message: 'Your Photo has been successfully updated.',
                type: 'success'
              })
              this.$store.dispatch('setUser', user)
              this.$router.push({
                name: 'UserProfile'
              })
            }, () => {
              Message({
                showClose: true,
                message: 'There was an error when trying to update your Photo.',
                type: 'error'
              })
            })
          } else {
            Message({
              showClose: true,
              message: 'Your photo url is empty or invalid',
              type: 'error'
            })
          }
        } else {
          if (!_.isEmpty(this.file.obj)) {
            var storageRef = db.ref('deals-photos/' + this.file.url)
            storageRef.put(this.file.obj).then((image) => {
              storageRef.getDownloadURL().then((url) => {
                var user = fireBase.auth().currentUser
                user.updateProfile({
                  photoURL: url
                }).then(() => {
                  Message({
                    showClose: true,
                    message: 'Your Photo has been successfully updated.',
                    type: 'success'
                  })
                  this.$store.dispatch('setUser', user)
                  this.$router.push({name: 'UserProfile'})
                }, () => {
                  Message({
                    showClose: true,
                    message: 'There was an error when trying to update your Photo.',
                    type: 'error'
                  })
                })
              })
            })
          } else {
            Message({
              showClose: true,
              message: 'The image file is empty or invalid.',
              type: 'error'
            })
          }
        }
      }
    }
  }
</script>



